<template>
  <div class="inspection">
    <SmallModule title="巡检统计(年度)">
      <!-- <template #titleRight>
        <div class="right-title-layout">
          <a-radio-group
            class="operation-screen-radio-group"
            v-model:value="searchInfo.dateRange"
            size="small"
            @change="handleInspectionChange"
          >
            <a-radio-button value="today">当日</a-radio-button>
            <a-radio-button value="thisMonth">本月</a-radio-button>
          </a-radio-group>
          <div class="detail-text"
            ><span class="text" @click="handPenetrate">详情</span
            ><span class="arrow">&gt;</span></div
          >
        </div>
      </template> -->
      <template #content>
        <div class="content-layout">
          <div class="data-layout">
            <DataIndicator label="应巡检" :value="indexData.planCount" @click="handPenetrate" />
            <DataIndicator
              label="已巡检"
              :value="indexData.inspectionCount"
              @click="handPenetrate"
            />
            <DataIndicator label="隐患缺陷" :value="indexData.hiddenCount" @click="handPenetrate" />
          </div>
          <div style="transform: scale(0.85); transform-origin: 0 0">
            <EchartLineBarChart
              :data="inspectionChartData"
              :width="`26.14vw`"
              :height="`23.3vh`"
              :category="inspectionChartData.categories"
              ybgcolor="#01B7D730"
              :name="inspectionChartData.chartSeriesName"
              :dataZoomDefaultValueRange="inspectionChartData.dataZoomDefaultValueRange"
              :dataZoomDisabled="false"
            />
          </div>
        </div>
      </template>
    </SmallModule>
  </div>
</template>

<script lang="ts" setup>
  import SmallModule from '/@/components/Framework/OperationScreen/SmallModule.vue';
  import DataIndicator from '/@/components/Framework/OperationScreen/DataIndicator.vue';
  import EchartLineBarChart from '/@/components/Framework/Chart/EchartLineBarChart.vue';
  import { ref, reactive, UnwrapRef, onMounted } from 'vue';

  import { assign } from 'lodash-es';
  import { getInspectionData } from './data';
  import { addTabPage } from '/@/utils/route';

  const searchInfo: UnwrapRef<any> = reactive({});
  const indexData: UnwrapRef<any> = reactive({});

  interface Props {
    operationSearchInfo?: {};
  }
  const props = withDefaults(defineProps<Props>(), {});

  function handPenetrate() {
    addTabPage('/bi/inspection/index');
  }

  onMounted(() => {
    assign(searchInfo, props.operationSearchInfo);
    // searchInfo.dateRange = 'today';
    queryInspectionData();
  });

  const inspectionChartData = ref({
    barData: [[], [], []],
    categories: [],
    units: ['(万Kw.h)', '(%)', '(%)'],
    chartSeriesName: [['应巡检', '已巡检', '隐患缺陷']],

    dataZoomDefaultValueRange: [0, 4],
  });

  // const handleInspectionChange = (e: Event) => {
  //   searchInfo.dateRange = e.target.value;
  //   queryInspectionData();
  // };

  const queryInspectionData = async () => {
    // searchInfo.selectValue = searchInfo.dateRange;
    let data = await getInspectionData(searchInfo);
    if (data.result) {
      assign(indexData, data.result);
      inspectionChartData.value.barData = data.result.barData;
      inspectionChartData.value.categories = data.result.categories;
    }
  };
</script>

